body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    background-color: #f8f9fa;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Header {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #343a40;
    height: 60px;
}

.Header .Title {
    max-width: 50%;
    color: white;
}

.logo {
    height: 60px;
}
.Body {
    height: 800px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.Body .Input {
    padding: 1rem;
    flex: 1 1;
}

.Body .Output {
    padding: 1rem;
    flex: 1 1;
}

.output-space {
    margin-top: 63px;
}
.input-wrapper {
    height: 100%;
}

.button-wrapper {
    float: right;
}

.input-button {
    margin: 8px;
    margin-right: 1px;
    width: 150px;
}
.output-wrapper {
    margin-bottom: 10px;
}

.output-value {
    background-color: white !important;
}


